import React, { useState, useEffect, useRef } from 'react';
import $ from 'jquery'; 

// Path2D for a Heart SVG
const heartSVG = "M0 200 v-200 h200 a100,100 90 0,1 0,200 a100,100 90 0,1 -200,0 z"
const SVG_PATH = new Path2D(heartSVG);

// Scaling Constants for Canvas
const SCALE = 0.05;
const OFFSET = 80;
export const canvasWidth = window.innerWidth;
export const canvasHeight = window.innerHeight;

export function draw(ctx, location){
  console.log("attempting to draw")
  ctx.fillStyle = location.contract ? 'red': 'blue';
  ctx.shadowColor = 'blue';
  ctx.shadowBlur = 15;
  ctx.save();
  ctx.scale(SCALE, SCALE);
  ctx.translate(location.x / SCALE - OFFSET, location.y / SCALE - OFFSET);
  ctx.rotate(225 * Math.PI / 180);
  ctx.fill(SVG_PATH);
  // .restore(): Canvas 2D API restores the most recently saved canvas state
  ctx.restore();  
};

export function useCanvas(){
    const canvasRef = useRef(null);
    const menuRef = useRef(null);

    const [coordinates, setCoordinates] = useState([]);

    const drawMenu = (position) =>{
        if(position){
            const menu = menuRef.current;
            $(menu).css({left:`${position.x}px`,top:`${position.y}px`});
        }
    }

    const doAddContract = () =>{
        console.log('test')
        // const currentCoord = { x: event.clientX, y: event.clientY, contract: true, recon: false };
        // setCoordinates([...coordinates, currentCoord]);
      }
    const doAddRecon = (event) =>{
    const currentCoord = { x: event.clientX, y: event.clientY, contract: false, recon: true };
    setCoordinates([...coordinates, currentCoord]);
    }
    const doRemove = (event) =>{
    
    }

    useEffect(()=>{
        const canvasObj = canvasRef.current;
        const ctx = canvasObj.getContext('2d');
        // clear the canvas area before rendering the coordinates held in state
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );

        drawMenu(coordinates.pop());

        // draw all coordinates held in state
        coordinates.forEach((coordinate)=>{
            if(coordinate.contract || coordinate.recon)
                draw(ctx, coordinate)
        });
        // coordinates.filter(x => x.recon || x.contract)
        // console.log(coordinates)
        if (menuRef && menuRef.current) {
            const menu = menuRef.current;
            $(menu).on("click", () => console.log('do something'))
        }

    });

    return [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight ];
}
